//个人和企业注册公共样式
.jl-form-agree{
  width: 410px;
  height: 56px;
  line-height: 56px;
  margin-top: 13px;
  cursor: pointer;
  .jl-check{
    position: relative;
    top: 3px;
    display:inline-block;
    margin-right: 13px;
    cursor: pointer;
    width: 13px;
    height: 13px;
    border: @border02;
    z-index: 10;
  }
  .jl-active{
    border: @border05;
    background:@check-color  url(../../Public/img/images/yes.png) no-repeat center;
    background-size: 13px 13px;
  }
}

.jl-reg-switch{
  width: 410px;
  height: 25px;
  padding-bottom: 60px;
  margin-left: 90px;
  a{
    position: relative;
    .jl-bg-r{
      position: absolute;
      top: 2px;
      left: 0;
      width: 22px;
      height: 21px;
      background: url(../../Public/img/images/register_p_bg.png) no-repeat left center;
    }
    span{
      position: absolute;
      left: 39px;
      top: 3px;
      width: 216px;
    }
    .jl-bg-c{
      position: absolute;
      top: 0;
      left: 0;
      width: 23px;
      height: 23px;
      background: url(../../Public/img/images/register_c_bg.png) no-repeat left center;
    }
    .font16(@color);
  }
}
.jl-reg-switch02{
  margin-left: 0;
}
.jl-reg-put{
  cursor: pointer;
  margin-bottom: 27px;
  width: 410px;
  height: 40px;
  background: @color;
  border: @border03;
  .font18(#fff);
}
.jl-form-agree{
  .jl-check{
    &.jl-check-active{
      border: 1px solid #00bff0;
      background: #00bff0 url(../../Public/img/tick.png) no-repeat 2px 3px;
      background-size: 9px 7px;
    }
  }
}



//企业注册
@import "../../Public/css/init.less";
.jl-c-address{
  outline: none;
  vertical-align: middle;
  cursor: pointer;
  box-sizing: border-box;
  position: relative;
  display: inline-block;
  width: 124px;
  height: auto;
  line-height: 38px;
  margin-right: 15px;
  .jl-c-c{
    .font14(@dark-grey);
  }
  .jl-top-tri{
    position: absolute;
    top: 10px;
    right: 20px;
    width:0;
    height:0;
    border-width:4px;
    border-style:solid;
    border-color:transparent transparent @dark-grey transparent;
  }
  .jl-bottom-tri{
    position: absolute;
    bottom: 10px;
    right: 20px;
    width:0;
    height:0;
    border-width:4px;
    border-style:solid;
    border-color:@dark-grey transparent transparent  transparent;
    cursor: pointer;
  }
}
.text-ellipsis{ white-space: nowrap; overflow: hidden; text-overflow: ellipsis}
.jl-y-select-title{
  position: relative;
  border: @border06;
  padding-left: 20px;
  padding-right: 22px;
}
.jl-c-address.jl-y-select-active .lj-option-box{
  display: block;
}
.lj-option-box{
  max-height: 300px;
  overflow: auto;
  display: none;
  width: 100%;
  position: absolute;
  top:100%;
  left: 0;
  z-index: 4;
  border-bottom: 1px solid #ddd;
}
.lj-option-box>li{
  box-sizing: border-box;
  width: 100%;
  background: white;
  border: @border06;
  border-bottom:none;
  border-top:none;
  .font14(@dark-grey);
  padding: 4px 0;
  padding-left: 17px;
  &:hover{
    background: @color;
    color:white;
  }
  &.lj-active-li{
    background: @color;
    color:white;
  }
}
.jl-y-select-input{
  position: absolute;
  width: 1px;
  height: 1px;
  opacity: 0;
}

.jl-check-txt{
