//用户协议
#jl-protocol{
  display: none;
  position: fixed;
  z-index:9999;
  top: 0;
  left: 0;
  width: 100%;
  height:100%;
}
.jl-background{
  width: 100%;
  height:100%;
  background: #000;
  filter:alpha(opacity=10);
  opacity: .1;
}
.js-protocol{
  width: 800px;
  height: 600px;
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  border:10px solid rgba(0,0,0,0.2);
  background:#fff;
}
.js-protocol-title{
  .font14(#fff);
  height: 40px;
  line-height: 40px;
  background: @color;
  padding-left: 16px;
  position: relative;
  .js-close{
    cursor: pointer;
    position: absolute;
    top: 14px;
    right: 20px;
    width: 14px;
    height: 14px;
    background:url("../../Public/img/sprite.png") no-repeat 0 -180px;
  }
}
.js-protocol-main{
  padding: 10px;
  float: left;
}
.js-js-protocol-content{
  padding: 10px;
  text-align: center;
  color:@dark-grey;
  overflow: auto;
  height: 418px;
  border:@border02;
  h2{
    display: inline-block;
    .font16(@dark-grey);
    font-weight: bold;
    text-align: center;
    margin-bottom: 15px;
    margin-top: 10px;
    border-bottom:1px solid @dark-grey;
  }
  h3,h4{
    text-align: left;
    line-height: 24px;
  }
  h3{
    font-weight: bold;
  }
  p{
    line-height: 24px;
    text-indent: @font-size*2;
    text-align: left;
  }
}

.js-protocol-footer{
  //height: 80px;
  text-align: center;
  float: left;
  width: 100%;
  .js-protocol-btn{
    width: 180px;
    height: 40px;
    background: @color;
    border: none;
    margin-top: 20px;
    .font18(#fff);
    cursor: pointer;
  }
}













